/***
  vds-listbox
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$color-listbox-text--hover: theme(listbox-text--hover);
$color-listbox-text--active: theme(listbox-text--active);
$color-listbox-text--focus: theme(listbox-text--focus);
$color-listbox-background: theme(listbox-background);
$color-listbox-background--active: theme(listbox-background--active);
$color-listbox-background--focus: theme(listbox-background--focus);
$color-listbox-background--hover: theme(listbox-background--hover);
$color-listbox-background-multi--selected: theme(listbox-bacground-multi--selected);
$color-listbox-border: theme(listbox-border);
$color-listbox-border--focus: theme(focus-outline-color);

.vds-listbox {
  @include default-listbox;
}

.vds-select--multi {
  .vds-listbox-item {
    padding: .25rem .875rem .25rem 0;

    &:focus,
    &:hover,
    &.vds-state--focus {
      &:not(.vds-state--disabled) {
        background-color: $color-listbox-background--hover;
        color: $color-listbox-text--hover;

        &::after {
          content: none;
        }
      }
    }

    .vds-checkbox-label {
      margin-left: .25rem;

      &::before {
        background-color: theme(#{transparent});
        border: 0;
      }

    }

    &.vds-state--selected,
    &[aria-selected='true'] {
      background-color: $color-listbox-background-multi--selected;

      &:hover {
        .vds-checkbox-label {
          color: $color-listbox-text--hover;
        }
      }
    }
  }

}
