/***
  vds-disclosure
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/
  @author pejin
*/

$color-disclosure-header-background: theme(disclosure-header);

.vds-disclosure {
  .vds-disclosure-content {
    margin: 0;
  }

  .vds-disclosure-inline {
    &.vds-disclosure-content {
      border-bottom: 1px solid $color-gray; // always show a bottom border on the disclosure-content
      padding: 0 8px 0 48px; // match icon footprint on the left for proper indentation
    }
  }

  .vds-disclosure-details {
    &.vds-disclosure-header {
      height: 48px;
      margin-bottom: 8px;
      border: 1px solid rgba(7, 120, 68, 0.1);

      .vds-disclosure-header-wrapper {
        display: flex;
        align-items: center;
        margin-left: 0;
        max-height: 32px;

        .vds-icon {
          margin-right: 8px;
        }
      }
    }
    &.vds-disclosure-content {
      padding: 0 16px;
    }
  }

  &-header {
    @include state-focus;
    background-color: $color-disclosure-header-background;
    align-items: center;
    border: 0;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    font-family: theme(font);
    min-height: 48px;
    line-height: px2em(20px);
    width: 100%;
    padding: 0; // remove default button padding
    border-bottom: 1px solid $color-gray; // show a bottom border when the header is collapsed

    &.vds-state--active {
      border-bottom: none; // remove the header's bottom border when expanded
    }

    &--reversed {
      flex-flow: row-reverse wrap;
      justify-content: space-between;
    }

    .vds-disclosure-header-icon {
      height: 16px;

      // icon footprint is 48px wide including the 8px left margin of the header-wrapper
      padding: 8px;
      margin-left: 8px;
    }

    .vds-disclosure-header-wrapper {
      font-size: 16px;
      text-align: left;
      margin-left: 8px;
    }

    &.focus-visible {
      outline-offset: -4px;
    }
  }

  &-text-button {
    @include state-focus;
    background-color: $color-disclosure-header-background;
    border: 0;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    font-family: theme(font);
    font-size: 14px;
    color: theme(link-color);
    height: 36px;
    line-height: px2em(20px);
    padding: 0;

    width: fit-content;
    text-decoration: theme(link-underline--hover);

    // link styles are used for this button
    &:hover,
    &:focus {
      color: theme(link-color--hover);
      text-decoration: theme(link-underline--hover);
    }
    &:active {
      color: theme(link-color--active);
    }
    &.vds-state--disabled {
      color: theme(link-color--disabled);
    }

    &:focus {
      outline-offset: 4px;
    }
  }
}
