/***
  vds-date-selector
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$color-date-selector-input-btn-icon-background--active: theme(date-picker-input-btn-icon-background--active);
$color-date-selector-input-btn-icon--readonly-disabled: theme(date-picker-input-btn-icon--readonly-disabled);
$color-date-selector-calendar: theme(date-picker-calendar);
$color-date-selector-calendar-shadow: theme(date-picker-calendar-shadow);
$color-date-selector-calendar-header-divider: theme(date-picker-calendar-header-divider);
$color-date-selector-calendar-dates-btn-text: theme(date-picker-calendar-dates-btn-text);
$color-date-selector-calendar-dates-btn-background: theme(date-picker-calendar-dates-btn-background);
$color-date-selector-calendar-dates-btn-background--focus-visible-hover: theme(date-picker-calendar-dates-btn-background--focus-visible-hover);
$color-date-selector-calendar-dates-btn-text--active: theme(date-picker-calendar-dates-btn-text--active);
$color-date-selector-calendar-dates-btn-background--active: theme(date-picker-calendar-dates-btn-background--active);
$color-date-selector-calendar-dates-btn-text--selected: theme(date-picker-calendar-dates-btn-text--selected);
$color-date-selector-calendar-dates-btn-background--selected: theme(date-picker-calendar-dates-btn-background--selected);
$color-date-selector-calendar-dates-btn-previous-and-next-month: theme(date-picker-calendar-dates-btn-previous-and-next-month);
$color-date-selector-calendar-dates-btn-previous-and-next-month--selected: theme(date-picker-calendar-dates-btn-previous-and-next-month--selected);
$color-date-selector-calendar-date-btn-label-border--current: theme(date-picker-calendar-date-btn-label-border--current);
$color-date-selector-calendar-date-btn-label-border--current-and-selected: theme(date-picker-calendar-date-btn-label-border--current-and-selected);


.vds-date-selector {
  &-header {
    align-items: center;
    border-bottom: 1px solid $color-date-selector-calendar-header-divider;
    display: flex;
    height: 44px;
  }

  &-day {
    color: $color-indigo;
    font-weight: 500;
    margin: 0 auto;
  }

  &-day-name {
    color: $color-indigo;
    height: 20px;
    padding: 8px 0;
  }

  &-header-title {
    color: $color-indigo;
    font-weight: 700;
    flex-grow: 1;
    text-align: center;
  }

  &-footer {
    text-align: right;
  }

  &-today {
    border: 1px solid $color-date-selector-calendar-date-btn-label-border--current;
  }

  &-next-prev-month {
    color: theme(date-picker-calendar-dates-btn-previous-and-next-month);
  }

  &-calendar {
    background-color: $color-date-selector-calendar;
    box-shadow: $color-date-selector-calendar-shadow;
    padding: 8px;
    z-index: 1;

    &-grid {
      width: 100%;
    }

    td {
      height: 44px;
      width: 44px;
    }
  }

  &-button-cancel {
    margin: 0;
  }

  &-in-range,
  &-mouse-enter-range {
    background-color: theme(button-icon-light-background--hover);
  }

  .vds-state {
    &-selected {
      color: $color-date-selector-calendar-dates-btn-text--selected;
      background-color: $color-date-selector-calendar-dates-btn-background--selected;

      &.vds-state-focused {
        color: $color-date-selector-calendar-dates-btn-text--selected;
        background-color: $color-date-selector-calendar-dates-btn-background--selected;
      }
    }

    &-disabled {
      color: $color-date-selector-input-btn-icon--readonly-disabled;
    }

    &-focused {
      background-color: theme(button-icon-light-background--hover);
    }

    &-highlighted {
      color: $color-date-selector-calendar-dates-btn-text--selected;
      background-color: $color-date-selector-calendar-header-divider;
    }
  }

  .vds-btn-label {
    pointer-events: none;
  }
}
